*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  font-family: "Gotham Pro", "Arial", "Helvetica", "Helvetica CY", "Nimbus Sans L", sans-serif;
  font-weight: 400;
  font-style: normal;
  display: flex;
  flex-direction: column;
}

body.js__body-no-scroll {
  overflow: hidden;
}

main {
  flex-grow: 1;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input::-ms-clear {
  display: none;
}

input:-moz-focusring {
  outline: none;
  border-color: #00adef;
  box-shadow: none;
}

input:invalid {
  outline: none;
  box-shadow: none;
}

ul li {
  list-style: none;
}

a {
  border-bottom: 0;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.wrapper {
  max-width: 1250px;
  margin: 0 auto;
  padding-left: 25px;
  padding-right: 25px;
}

@media (max-width: 959px) {
  .wrapper {
    padding-left: 24px;
    padding-right: 24px;
  }
}

* {
  outline: none;
}

.overflow-3lines {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  line-clamp: 3;
  box-orient: vertical;
}

.main__body {
  background-image: url("../img/page-bg.jpg");
  background-size: 100% auto;
  background-attachment: fixed;
  background-position: center;
  background-color: #fff;
}

@media (max-width: 639px) {
  .main__body {
    background-size: 200% auto;
  }
}

.btn-primary,
.btn-secondary,
.btn-small {
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  display: inline-block;
  height: auto;
  padding: 21px 24px 19px 24px;
  margin: 0;
  text-decoration: none;
  text-align: center;
  border: none;
  border-radius: 5px;
  box-sizing: border-box;
  transition: all .2s ease;
}

.btn-small {
  padding-top: 9px;
  padding-bottom: 9px;
}

.btn-primary,
.btn-small {
  background-color: #5ea9ff;
