    font-size: 16px;
    line-height: 21px;
    font-weight: 300;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .questions__item-content {
    font-size: 14px;
    line-height: 21px;
    font-weight: 300;
    padding-bottom: 20px;
  }
}

.footer {
  padding: 100px 0 130px;
  background: linear-gradient(rgba(255, 255, 255, 0.6)), url("../img/footer-bg.jpg") bottom center/cover no-repeat;
}

.footer__title {
  font-size: 40px;
  line-height: 48px;
  font-weight: 300;
  color: #000;
  margin: 0 auto 120px;
  max-width: 960px;
  text-align: center;
}

.footer__content {
  display: flex;
  justify-content: space-between;
  max-width: 940px;
  margin: 0 auto;
}

.footer__logo {
  font-size: 0;
  line-height: 0;
  font-weight: 300;
  width: 200px;
  padding: 4px;
  background-color: #fff;
  margin: 0 0 50px;
}

.footer__contacts-item:not(:last-child) {
  margin-bottom: 40px;
}

.footer__contacts-name {
  font-size: 18px;
  line-height: 21px;
  font-weight: 500;
  color: #000;
  margin: 0 0 7px;
}

.footer__contacts-desc, .footer__contacts-link {
  display: block;
  font-size: 18px;
  line-height: 21px;
  font-weight: 300;
  text-decoration: none;
  color: #000;
  margin: 0;
}

.footer__contacts-desc:not(:last-child), .footer__contacts-link:not(:last-child) {
  margin-bottom: 7px;
}

.footer__contacts-link {
  transition: color 0.3s;
}

.footer__contacts-link:hover {
  color: #00adeb;
}

.footer__certificate {
  display: flex;
  max-width: 450px;
}

.footer__certificate-img {
  width: 210px;
  height: 297px;
  margin-right: 30px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.1);
  flex-shrink: 0;
}

.footer__certificate-desc p:first-child {
  font-size: 14px;
  line-height: 21px;
  font-weight: 300;
  margin: 0 0 10px;
}

.footer__certificate-desc p:last-child {
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  margin: 0;
}

@media (max-width: 959px) {
  .footer__content {
    flex-direction: column;
    align-items: center;
  }
  .footer__title {
    max-width: 500px;
    margin-bottom: 55px;
  }
  .footer__logo {
    margin-bottom: 35px;
  }
  .footer__contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
  }
  .footer__contacts-item {
    text-align: center;
  }
  .footer__contacts-item:not(:last-child) {
    margin-bottom: 30px;
  }
  .footer__certificate {
    flex-direction: column;
    align-items: center;
  }
  .footer__certificate-img {
    margin: 0 0 25px;
  }
  .footer__certificate-desc {
    text-align: center;
  }
}

@media (max-width: 639px) {
  .footer {
    padding-top: 75px;
  }
  .footer__title {
    font-size: 32px;
    line-height: 38px;
    font-weight: 300;
  }
  .footer__contacts-name, .footer__contacts-link, .footer__contacts-desc {
    font-size: 16px;
    line-height: 21px;
  }
}

.to-top {
  margin: 0;
  padding: 0;
  height: auto;
  border: none;
  background-color: transparent;
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  color: #444;
  border-radius: 50%;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  opacity: 0;
  visibility: hidden;
  z-index: 100;
  transition: color 0.3s, opacity 0.3s, visibility 0s ease 0.3s;
}

.to-top:hover, .to-top:active, .to-top:focus {
  background-color: transparent;
}

.to-top rect {
  fill: #fff;
}

.to-top:hover {
  cursor: pointer;
  color: white;
}

.to-top:hover rect {
  transition: fill 0.7s;
  fill: #00adef;
}

.to-top.show {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

@media (max-width: 639px) {
  .to-top {
    transform: scale(0.8);
    transform-origin: right bottom;
  }
}

.mobile-control-mobile {
  background-color: #fff;
  padding-bottom: 100px;
  padding-top: 80px;
}

.mobile-control-mobile__title {
  font-size: 40px;
  line-height: 48px;
  font-weight: 300;
  color: #000;
  margin: 0 0 56px;
}

.mobile-control-mobile__list {
  margin: 0;
  padding: 0;
}

.mobile-control-mobile__item {
  border-top: 1px solid #eee;
}

.mobile-control-mobile__item:last-child {
  border-bottom: 1px solid #eee;
}

.mobile-control-mobile__item-top {
  position: relative;
  font-size: 18px;
  line-height: 24px;
  font-weight: 300;
  padding: 27px 60px 27px 0;
  color: #000;
  margin: 0;
}

.mobile-control-mobile__item-top:hover {
  cursor: pointer;
}

.mobile-control-mobile__item-top:hover svg {
  color: #00adeb;
}

.mobile-control-mobile__item-top.opened .mobile-control-mobile__item-icon {
  transform: rotate(45deg);
}

.mobile-control-mobile__item-icon {
  position: absolute;
  top: 50%;
  right: 0;
  width: 40px;
  height: 40px;
  margin-top: -20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s;
}

.mobile-control-mobile__item-icon svg {
  transition: color 0.3s;
  color: #000;
}

.mobile-control-mobile__item-content {
  font-size: 15px;
  line-height: 27px;
  font-weight: 300;
  max-width: 800px;
  padding-bottom: 40px;
  margin: 0;
  display: none;
}

.mobile-control-mobile__item-content ul {
  margin: 10px 0;
  padding: 0;
}

.mobile-control-mobile__item-content li {
  list-style: disc;
  list-style-position: inside;
}

@media (max-width: 639px) {
  .mobile-control-mobile__item-top {
    font-size: 16px;
    line-height: 21px;
    font-weight: 300;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}
